import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import "./Dealers.css";
import "../assets/style.css";

const PostReview = () => {
  const [dealer, setDealer] = useState({});
  const [review, setReview] = useState("");
  const [model, setModel] = useState("");
  const [year, setYear] = useState("");
  const [date, setDate] = useState("");
  const [rating, setRating] = useState(0);
  const [carmodels, setCarmodels] = useState([]);

  let params = useParams();
  let id = params.id;
  // Assuming backend runs on localhost:8000, update URLs to absolute backend URLs
  let dealer_url = `http://localhost:8000/djangoapp/dealer/${id}`;
  let review_url = `http://localhost:8000/djangoapp/dealer/${id}/add_review/`;
  let carmodels_url = `http://localhost:8000/djangoapp/get_cars`;

  const postreview = async (event) => {
    if(event) event.preventDefault();
    let name = sessionStorage.getItem("firstname")+" "+sessionStorage.getItem("lastname");
    if(name.includes("null")) {
      name = sessionStorage.getItem("username");
    }
    if(!model || review === "" || date === "" || year === "" || rating === 0) {
      alert("All details including rating are mandatory");
      return;
    }

    let model_split = model.split(" ");
    let make_chosen = model_split[0];
    let model_chosen = model_split[1];

    let jsoninput = JSON.stringify({
      "name": name,
      "dealer_id": id,
      "review": review,
      "rating": rating,
      "purchase": true,
      "purchase_date": date,
      "car_make": make_chosen,
      "car_model": model_chosen,
      "car_year": year,
    });

    console.log(jsoninput);
    try {
      const res = await fetch(review_url, {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        credentials: "include",
        body: jsoninput,
      });

      if (res.status === 403) {
        alert("You must be logged in to post a review.");
        return;
      }
      const contentType = res.headers.get("content-type");
      if (contentType && contentType.indexOf("application/json") !== -1) {
        const json = await res.json();
        if (json.status === 200) {
          window.location.href = window.location.origin+"/dealer/"+id;
        } else {
          alert("Failed to post review: " + (json.message || "Unknown error"));
        }
      } else {
        const text = await res.text();
        alert("Failed to post review. Response is not JSON: " + text);
      }
    } catch (error) {
      alert("Error posting review: " + error.message);
    }
  }

  const get_dealer = async () => {
    const res = await fetch(dealer_url, {
      method: "GET"
    });
    const retobj = await res.json();

    if(retobj.status === 200) {
      let dealerobjs = Array.from(retobj.dealer)
      if(dealerobjs.length > 0)
        setDealer(dealerobjs[0])
    }
  }

  const get_cars = async () => {
    const res = await fetch(carmodels_url, {
      method: "GET"
    });
    const retobj = await res.json();

    let carmodelsarr = Array.from(retobj.CarModels)
    setCarmodels(carmodelsarr)
  }

  useEffect(() => {
    get_dealer();
    get_cars();
  },[]);

  return (
    <div style={{margin:"5%"}}>
      <h1 style={{color:"darkblue"}}>{dealer.full_name}</h1>
      <textarea id='review' cols='50' rows='7' placeholder="Write your review here..." onChange={(e) => setReview(e.target.value)}></textarea>
      <div className='input_field'>
        Purchase Date <input type="date" onChange={(e) => setDate(e.target.value)}/>
      </div>
      <div className='input_field'>
        Car Make 
        <select name="cars" id="cars" onChange={(e) => setModel(e.target.value)}>
          <option value="" selected disabled hidden>Choose Car Make and Model</option>
          {carmodels.map(carmodel => (
            <option key={carmodel.CarMake + carmodel.CarModel} value={carmodel.CarMake+" "+carmodel.CarModel}>{carmodel.CarMake} {carmodel.CarModel}</option>
          ))}
        </select>        
      </div >
      <div className='input_field'>
        Car Year <input type="number" onChange={(e) => setYear(e.target.value)} max={2023} min={2015}/>
      </div>
      <div className='input_field'>
        Rating: 
        <select value={rating} onChange={(e) => setRating(parseInt(e.target.value))}>
          <option value={0} disabled>Select rating</option>
          <option value={1}>1 - Poor</option>
          <option value={2}>2 - Fair</option>
          <option value={3}>3 - Good</option>
          <option value={4}>4 - Very Good</option>
          <option value={5}>5 - Excellent</option>
        </select>
      </div>
      <div>
        <button className='postreview' onClick={postreview}>Post Review</button>
      </div>
    </div>
  )
}

export default PostReview
